@charset "UTF-8";
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
  list-style: none;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  color: inherit;
  text-decoration: none;
}

input,
textarea {
  border: none;
  background-color: transparent;
}

textarea {
  resize: none;
}

address,
cite {
  font-style: normal;
}

html {
  scroll-behavior: smooth;
}

button {
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

input[type=text]::-ms-clear {
  display: none;
  width: 0;
  height: 0;
}

input[type=text]::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  display: none;
}

input,
a,
button,
textarea,
select {
  outline: transparent;
}

input:focus,
textarea:focus {
  outline: 1px solid #0070ba;
}

html,
body {
  height: 100%;
}

.site-wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.main {
  flex: 1 1 auto;
}

:root {
  --color-black: #000;
  --color-white: #fff;
  --color-accent: #e57c5d;
  --color-dark: #282828;
  --color-bg: #f2f2f2;
}

@font-face {
  font-family: "TT Commons";
  src: url("../fonts/TTCommons-Regular.woff2") format("woff2"), url("../fonts/TTCommons-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT Commons";
  src: url("../fonts/TTCommons-Bold.woff2") format("woff2"), url("../fonts/TTCommons-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT Commons";
  src: url("../fonts/TTCommons-DemiBold.woff2") format("woff2"), url("../fonts/TTCommons-DemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT Commons";
  src: url("../fonts/TTCommons-Medium.woff2") format("woff2"), url("../fonts/TTCommons-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT Commons";
  src: url("../fonts/TTCommons-Light.woff2") format("woff2"), url("../fonts/TTCommons-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT Commons";
  src: url("../fonts/TTCommons-Thin.woff2") format("woff2"), url("../fonts/TTCommons-Thin.woff") format("woff");
  font-weight: 100;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT Commons";
  src: url("../fonts/TTCommons-ExtraBold.woff2") format("woff2"), url("../fonts/TTCommons-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/Oswald-Bold.woff2") format("woff2"), url("../fonts/Oswald-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
body {
  font-family: "TT Commons";
  font-weight: 400;
  font-size: 17px;
  line-height: 1.5;
  color: var(--color-dark);
  background-color: var(--color-white);
  position: relative;
  overflow-x: hidden;
}
@media (max-width: 767.98px) {
  body {
    font-size: 15px;
  }
}
body.no-scroll {
  overflow: hidden;
}

.container {
  max-width: 1270px;
  padding: 0px 15px;
  margin: 0 auto;
}

.card-link {
  transition: 200ms ease;
  color: inherit;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.main.mt {
  margin-top: 110px;
}
@media (max-width: 991.98px) {
  .main.mt {
    margin-top: 75px;
  }
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  z-index: 10;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.55);
  backdrop-filter: blur(5px);
  transition: 200ms ease-in;
}
.overlay.active {
  opacity: 1;
  visibility: visible;
  transition-delay: 550ms ease;
}

.logo {
  width: 265px;
  height: 50px;
  display: block;
  transition: 200ms ease;
}
@media (max-width: 1270px) {
  .logo {
    width: 200px;
    height: 40px;
  }
}
.logo:hover {
  opacity: 0.8;
}
.logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.btn {
  display: inline-block;
  background-color: var(--color-accent);
  color: var(--color-white);
  padding: 15px 25px 12px 25px;
  border-radius: 5px;
  line-height: 1;
  text-align: center;
  transition: 200ms ease;
}
.btn:hover {
  background-color: #de5831;
}
.btn:active {
  transform: scale(0.98);
}
.btn--transparent {
  background-color: transparent;
  border: 2px solid var(--color-white);
  border-radius: 5px;
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 15px;
  padding-bottom: 12px;
}
@media (max-width: 1350px) {
  .btn--transparent {
    padding-left: calc(
        20px + 20 *
          ((100vw - 375px) / 975)
      );
  }
}
@media (max-width: 1350px) {
  .btn--transparent {
    padding-right: calc(
        20px + 20 *
          ((100vw - 375px) / 975)
      );
  }
}
@media (max-width: 1350px) {
  .btn--transparent {
    padding-top: calc(
        10px + 5 *
          ((100vw - 375px) / 975)
      );
  }
}
@media (max-width: 1350px) {
  .btn--transparent {
    padding-bottom: calc(
        7px + 5 *
          ((100vw - 375px) / 975)
      );
  }
}
.btn--transparent:hover {
  background-color: var(--color-white);
  color: var(--color-dark);
}

.map iframe {
  width: 100%;
  height: 100%;
  border: 0;
}

.card-link {
  transition: 200ms ease;
  color: inherit;
}

.choices {
  font-size: 14px;
  line-height: 1.1;
  height: 55px;
  font-weight: 400;
  width: 100%;
  color: #686868;
  margin-bottom: 0 !important;
  padding-right: 20px;
}
.choices__placeholder {
  opacity: 1;
}
.choices__inner {
  padding: 12px;
  padding-left: 20px;
  width: 100%;
  height: 100%;
  background-color: inherit !important;
  display: flex;
  align-items: center;
  position: relative;
  border: none;
}
.choices::after {
  border: none !important;
  width: 25px !important;
  height: 25px !important;
  margin-top: 0 !important;
  mask-image: url("../img/chevron.svg");
  mask-repeat: no-repeat;
  mask-position: center;
  mask-size: 100%;
  background-color: #515253;
  right: 5px !important;
  top: 20px !important;
  transition: 150ms ease-in;
}
.choices.is-open::after {
  margin-top: 0 !important;
  transform: rotate(180deg);
}
.choices__list {
  padding: 0;
  background-color: inherit;
  max-height: 200px;
}
.choices__list .choices__item {
  background-color: inherit;
}
.choices__list--dropdown {
  z-index: 100;
  z-index: 5;
}
.choices__list--dropdown .choices__list {
  max-height: 200px !important;
  overflow-y: auto;
}
.choices__list--dropdown .choices__item {
  position: relative;
  padding: 12px !important;
  padding-right: 0 !important;
}
.choices__list--dropdown .choices__item:hover {
  background-color: var(--color-bg);
  color: var(--color-dark);
}
.choices__list--dropdown .choices__item .choices__item--selectable {
  background-color: #ededed;
}
.choices__list--dropdown .choices__item.is-selected:focus, .choices__list--dropdown .choices__item.is-highlighted:focus {
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.05);
}
.choices__list--dropdown .choices__item.is-selected:hover, .choices__list--dropdown .choices__item.is-highlighted:hover {
  background-color: var(--color-bg);
}

.donation-block {
  background-color: var(--color-white);
  color: var(--color-dark);
  border-radius: 10px;
  max-width: 425px;
  width: 100%;
  padding: 30px;
}
@media (max-width: 1350px) {
  .donation-block {
    padding: calc(
        20px + 10 *
          ((100vw - 375px) / 975)
      );
  }
}
.donation-block__top {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 15px;
  margin-bottom: 30px;
}
@media (max-width: 1350px) {
  .donation-block__top {
    margin-bottom: calc(
        20px + 10 *
          ((100vw - 375px) / 975)
      );
  }
}
.donation-block__text {
  margin-bottom: 15px;
  line-height: 1.3;
  color: #323232;
  font-size: 16px;
}
@media (max-width: 1350px) {
  .donation-block__text {
    font-size: calc(
        12px + 4 *
          ((100vw - 375px) / 975)
      );
  }
}
.donation-block__btn {
  padding: 15px 15px 13px 15px;
  width: 100%;
  background-color: #6e9ecf;
  border-radius: 5px;
  color: var(--color-white);
  line-height: 1;
  transition: 200ms ease;
  font-size: 19px;
}
@media (max-width: 1350px) {
  .donation-block__btn {
    font-size: calc(
        15px + 4 *
          ((100vw - 375px) / 975)
      );
  }
}
.donation-block__btn:hover {
  background-color: #4884c2;
}
.donation-block__btn--once {
  background-color: #e8f1fa;
  color: #6e9ecf;
}
.donation-block__btn--once:hover {
  background-color: #4884c2;
  color: var(--color-white);
}
.donation-block__further {
  width: 100%;
  height: 53px;
}
@media (max-width: 1350px) {
  .donation-block__further {
    height: calc(
        42px + 11 *
          ((100vw - 375px) / 975)
      );
  }
}
.donation-block__sum {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-bottom: 25px;
  gap: 12px;
}
@media (max-width: 1350px) {
  .donation-block__sum {
    gap: calc(
        10px + 2 *
          ((100vw - 375px) / 975)
      );
  }
}
.donation-block__label {
  grid-column: span 2;
  position: relative;
}
.donation-block__label::after {
  position: absolute;
  content: "₽";
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  color: #6e9ecf;
  font-size: 18px;
}
.donation-block__input {
  display: block;
  width: 100%;
  color: var(--color-dark);
  border: 1px solid #c8cbce;
  border-radius: 5px;
  padding: 15px 50px 15px 20px;
  height: 47px;
}
@media (max-width: 1350px) {
  .donation-block__input {
    height: calc(
        37px + 10 *
          ((100vw - 375px) / 975)
      );
  }
}
.donation-block__input:focus, .donation-block__input:active {
  border-color: var(--color-dark);
  outline: transparent;
}

.sum-checkbox {
  cursor: pointer;
  color: var(--color-dark);
  width: 100%;
  height: 47px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
}
@media (max-width: 1350px) {
  .sum-checkbox {
    height: calc(
        37px + 10 *
          ((100vw - 375px) / 975)
      );
  }
}
.sum-checkbox__input {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
.sum-checkbox__box {
  width: 100%;
  height: 100%;
  border: 1px solid #c8cbce;
  border-radius: 5px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 13px;
}
.sum-checkbox__input:checked + .sum-checkbox__box {
  background-color: var(--color-accent);
  color: var(--color-white);
  border-color: transparent;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  transition: 200ms ease;
  background-color: var(--color-white);
  padding-top: 30px;
  padding-bottom: 30px;
}
@media (max-width: 1350px) {
  .header {
    padding-top: calc(
        15px + 15 *
          ((100vw - 375px) / 975)
      );
  }
}
@media (max-width: 1350px) {
  .header {
    padding-bottom: calc(
        15px + 15 *
          ((100vw - 375px) / 975)
      );
  }
}
@media (max-width: 767.98px) {
  .header {
    top: -5px;
  }
}
.header.scroll-header {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
  padding-top: 15px;
  padding-bottom: 15px;
}
@media (max-width: 1350px) {
  .header.scroll-header {
    padding-top: calc(
        10px + 5 *
          ((100vw - 375px) / 975)
      );
  }
}
@media (max-width: 1350px) {
  .header.scroll-header {
    padding-bottom: calc(
        10px + 5 *
          ((100vw - 375px) / 975)
      );
  }
}
.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  margin-right: 60px;
}
@media (max-width: 1270px) {
  .header__logo {
    margin-right: calc(15px + 45 * (100vw - 991px) / 279);
  }
}
.header__menu {
  margin-left: auto;
  margin-right: 10px;
}
.header__toggle {
  flex-shrink: 0;
  width: 28px;
  height: 28px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  transition: 200ms ease-in;
  position: relative;
  border-radius: 3px;
  display: none;
}
@media (max-width: 991.98px) {
  .header__toggle {
    display: flex;
  }
}
.header__toggle-line {
  border-radius: 3px;
  width: 100%;
  height: 2px;
  background-color: var(--color-accent);
  position: relative;
  transition: 200ms ease-in;
}
.header__toggle-line::after, .header__toggle-line::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  left: 0;
  background-color: inherit;
  transition: 200ms ease-in;
  border-radius: 3px;
}
.header__toggle-line::after {
  top: -8px;
}
.header__toggle-line::before {
  bottom: -8px;
}

.footer {
  padding-top: 40px;
  background-color: var(--color-bg);
}
.footer__logo {
  width: 265px;
  height: 50px;
}
@media (max-width: 1350px) {
  .footer__logo {
    width: calc(
        150px + 115 *
          ((100vw - 375px) / 975)
      );
  }
}
@media (max-width: 1350px) {
  .footer__logo {
    height: calc(
        30px + 20 *
          ((100vw - 375px) / 975)
      );
  }
}
.footer__top {
  display: grid;
  grid-template-columns: auto minmax(auto, 550px) auto;
  justify-content: space-between;
  gap: 25px;
  margin-bottom: 30px;
}
@media (max-width: 991.98px) {
  .footer__top {
    grid-template-columns: auto auto;
    align-items: center;
  }
}
@media (max-width: 359.98px) {
  .footer__top {
    gap: 20px;
  }
}
.footer__lists {
  display: grid;
  grid-template-columns: repeat(3, auto);
  justify-content: space-between;
  gap: 25px;
}
@media (max-width: 991.98px) {
  .footer__lists {
    display: none;
  }
}
.footer__phone {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding-left: 85px;
  position: relative;
  line-height: 1;
}
.footer__phone:hover::after {
  background-color: #6e9ecf;
}
@media (max-width: 1270px) {
  .footer__phone {
    padding-left: 75px;
  }
}
@media (max-width: 767.98px) {
  .footer__phone {
    padding-left: 0;
  }
}
.footer__phone::after {
  position: absolute;
  content: "";
  width: 60px;
  height: 60px;
  top: 0;
  left: 0;
  background-color: #e6e6e6;
  transition: 200ms ease;
  mask: url("../img/phone-big.svg") no-repeat center/contain;
}
@media (max-width: 767.98px) {
  .footer__phone::after {
    display: none;
  }
}
.footer__phone-numbers {
  line-height: 1;
  font-weight: 700;
  color: #6e9ecf;
  font-family: "Oswald";
  font-size: 30px;
  margin-bottom: 15px;
}
@media (max-width: 1350px) {
  .footer__phone-numbers {
    font-size: calc(
        18px + 12 *
          ((100vw - 375px) / 975)
      );
  }
}
@media (max-width: 1350px) {
  .footer__phone-numbers {
    margin-bottom: calc(
        10px + 5 *
          ((100vw - 375px) / 975)
      );
  }
}
.footer__phone-text {
  font-size: 15px;
  color: #888888;
}
@media (max-width: 1350px) {
  .footer__phone-text {
    font-size: calc(
        11px + 4 *
          ((100vw - 375px) / 975)
      );
  }
}
.footer__link {
  display: block;
  padding: 7px 10px;
  transition: 200ms ease;
}
.footer__link:hover {
  color: var(--color-accent);
}
.footer__bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-top: 1px solid #dfdfdf;
  color: #9c9c9c;
  font-size: 15px;
  padding-top: 25px;
  padding-bottom: 25px;
}
@media (max-width: 1350px) {
  .footer__bottom {
    font-size: calc(
        13px + 2 *
          ((100vw - 375px) / 975)
      );
  }
}
@media (max-width: 1350px) {
  .footer__bottom {
    padding-top: calc(
        20px + 5 *
          ((100vw - 375px) / 975)
      );
  }
}
@media (max-width: 1350px) {
  .footer__bottom {
    padding-bottom: calc(
        20px + 5 *
          ((100vw - 375px) / 975)
      );
  }
}
@media (max-width: 991.98px) {
  .footer__bottom {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
}
.footer__copy {
  margin-right: 30px;
  line-height: 1.3;
}
@media (max-width: 991.98px) {
  .footer__copy {
    margin-right: 0;
    margin-bottom: 20px;
  }
}
.footer .company {
  display: flex;
  align-items: center;
  text-decoration: none;
  font-size: 15px;
}
.footer .company span {
  margin-right: 10px;
}
@media (max-width: 767.98px) {
  .footer .company {
    justify-content: center;
  }
  .footer .company img {
    width: 140px;
  }
}

.mobile-menu {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: absolute;
  left: 0;
  top: 0;
  height: 100vh;
  background-color: var(--color-accent);
  color: var(--color-white);
  z-index: 12;
  width: 100%;
  transform: translateY(-100%);
  opacity: 0;
  visibility: hidden;
  transition: 350ms ease-in-out;
  overflow-y: auto;
  padding: 40px;
  padding-bottom: 20px;
}
@media (max-width: 1350px) {
  .mobile-menu {
    padding: calc(
        20px + 20 *
          ((100vw - 375px) / 975)
      );
  }
}
.mobile-menu.active {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}
.mobile-menu__top {
  margin-bottom: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.mobile-menu__close {
  width: 30px;
  height: 30px;
}
.mobile-menu__close svg {
  width: 100%;
  height: 100%;
}
.mobile-menu__close svg path {
  transition: 200ms ease;
  stroke: var(--color-light);
}
.mobile-menu__list {
  margin-bottom: 32px;
  width: 100%;
}
.mobile-menu__item:not(:last-child) {
  margin-bottom: 15px;
}
.mobile-menu__link {
  font-size: 18px;
  width: 100%;
  display: block;
  color: var(--color-light);
